<template>
  <div class="ml-3">
    <i title="" style="cursor:pointer" class="fa fa-tools" @click="showBtn=!showBtn"></i>
    <div v-if="showBtn">
      <b-button size="sm" @click="seed" variant="primary" class="mr-1"></b-button>
      <b-button size="sm" @click="clear" variant="danger"></b-button>
    </div>
  </div>
</template>

<script>
import runMock from '@/mock'
import { clear } from '@/mock'

export default {
  data() {
    return {
      showBtn: false
    }
  },
  inject: ['refresh'],
  methods: {
    async seed() {
      let res = await this.$swal({
        title: '',
        text: '',
        icon: 'question',
        showCancelButton: true,
        confirmButtonText: '',
        cancelButtonText: '',
      })
      if(res.isConfirmed) {
        runMock()
        this._refresh()
      }
    },
    async clear() {
      let res = await this.$swal({
        title: '',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: '',
        cancelButtonText: '',
        cancelButtonColor: '#d33',
      })
      if(res.isConfirmed) {
        clear()
        this._refresh()
      }
    },
    _refresh() {
      this.showBtn = false
      this.$store.commit('refreshUser')
      this.$store.commit('refreshArticles')
      this.refresh()
      // window.location.reload(true)
    },
  }
}
</script>
