<template>
  <b-navbar-nav v-if="isLogined" class="ml-auto">
    <b-nav-item-dropdown class="mr-3" right no-caret html="<i class='fa fa-plus'></i>">
      <b-dropdown-item to="/articles/create">
        <i class="fa fa-pen"></i>
        
      </b-dropdown-item>
    </b-nav-item-dropdown>

    <b-avatar :src="user.avatar"></b-avatar>
    <b-nav-item-dropdown :text="user.name" right>
      <b-dropdown-item :to="`/column/${user.id}`">
        <i class="fa fa-list-ul"></i>
        
      </b-dropdown-item>
      <b-dropdown-item :to="`/users/${user.id}/edit`">
        <i class="fa fa-user-edit"></i>
        
      </b-dropdown-item>
      <b-dropdown-item @click="logout" href="#">
        <i class="fa fa-sign-out"></i>
        
      </b-dropdown-item>
    </b-nav-item-dropdown>
  </b-navbar-nav>

  <b-navbar-nav v-else class="ml-auto">
    <b-link to="/auth/login" class="btn btn-outline-info mr-2">
      <i class="fa fa-sign-in"></i>
      
    </b-link>
    <b-link to="/auth/register" class="btn btn-outline-info">
      <i class="fa fa-user-plus"></i>
      
    </b-link>
  </b-navbar-nav>
</template>

<script>
import {mapGetters} from 'vuex'

export default {
  computed: {
    ...mapGetters(['isLogined', 'user']),
  },
  created() {
  },
  methods: {
    logout() {
      this.$swal({
        title: '',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: '',
        cancelButtonText: '',
        cancelButtonColor: '#d33',
      }).then(res => {
        if(res.isConfirmed) {
          this.$store.dispatch('logout')
        }
      })
    },
  }
}
</script>
